
import { useToast } from "@/functions";
import { auth } from "@/settings/firebase";
import { computed, getCurrentInstance, inject, PropType, ref } from "vue";
import { Account } from "@/models";
import { useStore } from "vuex";

export default {
  name: "ProfileInformationComponent",
  props: {
    account: {
      type: Object as PropType<Account>,
      required: true
    }
  },
  setup(props: any) {
    const store = useStore();
    const toast = useToast();

    const userId = auth.currentUser?.uid;
    const username = ref("");

    const isDirty = computed(
      () =>
        username.value !== props?.account?.username &&
        username.value?.length >= 3
    );

    const changeUsername = () => {
      const payload = {
        userId: userId,
        username: username.value
      };
      store.dispatch("updateUsername", payload).then(() => {
        toast("Changed Username", { type: "success" });
        username.value = "";
      });
    };

    return {
      username,
      userId,
      isDirty,
      changeUsername
    };
  }
};
